/* Image.js */
@media screen and (min-device-width: 400px) and (max-device-width:1200px) {
    .div1{
        width: 400px;
    }
    .div1 img{
        width: 300px;
        height: auto;
    }
}
.div1{
    margin-left:100;
    margin-right:100;
    border-top-width:3;
    border-top-color:blue;
    border-top-style:solid;
    border-bottom-width:2;
    border-bottom-color:#ccc;
    border-bottom-style:solid
}
