@media screen and (min-device-width: 400px) and (max-device-width:1200px) {
    .header1{
        height: 5px;
        line-height: 48px;
        background-color:#93BCD8;
        text-align: center; 
        margin-left:2%;
        
    }
    nav{
        width: 400px; 
        margin: 0 auto; 
    }
    .header1 a{
        display: inline-block;
        text-align: center;
        height: 48px;
        color: #fff;  
        margin-left: 4px;
        width: 40px;
        font-size: 6px;
    }
    .header1 .active{
        border-bottom: 2px solid #CEE9B6;
        color:  #CEE9B6;
    }
}
