*{
    margin: 0;
    padding: 0
}
.div{
    width:25%;
    height:300px;
    border: 1px grey solid;
    position: absolute;
    top:200px;
    left:35%;
}
.username{
    position:relative;
}
.password{
    position:relative;
}
#login{
    display: block;
    margin: 60px auto 0;
    width: 25%;
    height: 40px;
    line-height: 40px;
    font-size: 16px;
    color: #fff;
    text-align: center;
    cursor: pointer;
    background: #3D84FF;
}
.userid{
    height:30px;
    width:300px;
    margin-left:25px;
    margin-top:40px;
    background-color:#EEEEEE
}
.pass{
    height:30px;
    width:300px;
    margin-left:25px;
    margin-top:40px;
    background-color:#EEEEEE
}
