.title{
    margin-top: 20px;
    text-align: center;
}
.title li{
    height: 50px;
    line-height: 50px;
    display: inline-block;
    text-align: center;
    padding: 0 5px;
    width: 200px;
    border-left: solid 1px #000;
    border-top: solid 1px #000;
    border-bottom: solid 1px #000;
    background-color: #8FC798;
}
.title li:last-child{
    border-right: solid 1px #000;
}
.con{
    text-align: center;
}
.con li{
    height: 50px;
    line-height: 50px;
    display: inline-block;
    text-align: center;
    padding: 0 5px;
    width: 200px;
    border-left: solid 1px #000;
    border-bottom: solid 1px #000;
}
.con li:last-child{
    border-right: solid 1px #000;
}

.box{
    margin-left:25%;
}
.tit{
    margin-top:50px;
    font-size:23px;
}
.content{
    width:600px;
    border-top:2px #93BCD8 solid
}
.image{
    width:170px;
    height:170px;
    float:right;
    margin-top:20px;
    margin-right:30px;
    border-radius:100px
}
.repair{
    width:200px;
