@media screen and (min-width: 4001px) {
    .head_Top {width:100% ;height: 720px;background: antiquewhite;}
}
@media screen and (max-width: 4000px) {
    .head_Top {width:100% ;height: 420px;background: antiquewhite;}
}
@media screen and (max-width: 2999px) {
    .head_Top {width:100% ;height: 350px;background: antiquewhite;}
}
@media screen and (max-width: 2549px) {
    .head_Top {width:100% ;height: 300px;background: antiquewhite;}

}@media screen and (max-width: 2200px) {
    .head_Top {width:100% ;height: 300px;background: antiquewhite;}
}
@media screen and (max-width: 1920px) {
    .head_Top {width:100% ;height: 180px;background: antiquewhite;}
}
/* css 注释说明：设置了浏览器宽度不小于1201px时 head_Top 显示280px高度 */
@media screen and (max-width: 1200px) {
    .head_Top {width:100% ;height: 130px;background: antiquewhite;}
}
/* 设置了浏览器宽度不大于1200px时 head_Top 显示150px高度 */
@media screen and (max-width: 900px) {
    .head_Top {width:100% ;height: 95px;background: antiquewhite;}
}
/* 设置了浏览器宽度不大于900px时 head_Top 显示200px高度 */
@media screen and (max-width: 500px) {
    .head_Top {width:100% ;height: 40px;background: antiquewhite;}
}
*{
    padding: 0;
    margin: 0;
}
ul,li{
	list-style: none;
}
a{
    text-decoration: none; 
}
.h{
    height: 30px;
    line-height: 28px;
    background-color:#5E9487;
}
.n{
    width: 60%; 
}
.h a{
    display: inline-block;
    text-align: center;
    height: 28px;
    padding: 0 5px;
    color: #fff; 
    width: 120px;
    font-size: small;
}
.h .active{
    border-bottom: 2px solid #CEE9B6;
    color: #CEE9B6;
}
.box{
    margin-left:25%;
}
.tit{
    margin-top:50px;
    font-size:23px;
}
.content{
    width:600px;
    border-top:2px #93BCD8 solid
}
.image{
    width:170px;
    height:170px;
    float:right;
    margin-top:20px;
    margin-right:30px;
    border-radius:100px
}
.detail{
    margin-left: 200px;
}
.money{
    width:100px;
    height:20px
}
.btn{
    width:100px;
    height:50px;
    margin-top:20px;
    font-size:17px;
    background-color:#93BCD8;
    border:0;
    border-radius:15px
}
/* 二维码 */
.big{
    width:100%;
    margin-left:25%
}
.code{
    margin-top:80px;
    margin-left:220px
}
.btn2{
    position:absolute;
    width:100px;
    height:50px;
    margin-top:250px;
    font-size:15px;
    margin-left:-120px;
    background-color:#93BCD8;
    border:0;
    border-radius:15px
}